@import url(https://fonts.googleapis.com/css?family=Lato);
html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: 'Lato', Arial, Helvetica, sans-serif;
  color: #333;
  background: #e3e3e3;
  font-size: 16px;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
#root,
.App {
  width: 100%;
  background: #e3e3e3;
}

.content {
  max-width: 960px;
  margin: 0 auto;
}
.nav {
  background: #008542;
  overflow-y: auto;
}
h1 {
  margin: 0;
  background-color: #f2f2f2;
  line-height: 50px;
  padding: 0 20px;
}
h2 {
}
.tab button {
  border: 0;
  background: none;
}
.tab.location {
  display: flex;
}
.tab.location li {
  flex: 0 0 100px;
  min-width: 100px;
}
.tab.location li button {
  color: #fff;
  font-size: 1.125rem;
  height: 54px;
  width: 100%;
  border-right: 1px solid #fff;
}
.tab.location li button.clicked {
  background: #20b168;
}

.tab.type {
  display: flex;
  overflow-y: auto;
}
.tab.type li {
  flex: 0 0 33.3%;
  box-sizing: border-box;
}
.tab.type li button {
  color: #fff;
  background: #ccc;
  font-size: 18px;
  height: 54px;
  width: 100%;
  border-right: 1px solid #fff;
}
.tab.type li button.clicked {
  color: #008542;
  background: #fff;
  border-top: 3px solid #008542;
}
.main {
  background: #fff;
}
.list {
  display: flex;
  flex-wrap: wrap;
}
.list li {
  padding: 0.6rem;
  flex: 1 0 100%;
  min-width: 100%;
  box-sizing: border-box;
}
.list li button {
  text-align: left;
  width: 100%;
  background: none;
  border: 3px solid #e6e6e6;
  box-sizing: border-box;
}
.raceName {
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
}
.raceNumber {
  font-size: 1rem;
  color: #008542;
  margin: 0.5rem 0 0 0;
}
.meetingDate {
  font-size: 0.75rem;
  color: #9e9e9e;
  font-style: italic;
}
.meetingName {
  border-top: 1px solid #e6e6e6;
  margin-top: 0.5rem;
  padding-top: 0.3rem;
}
@media only screen and (min-width: 640px) {
  .list li {
    flex-basis: 50%;
    min-width: 50%;
  }
}

@media only screen and (min-width: 960px) {
  .list li {
    flex-basis: 33.33%;
    min-width: 33.33%;
  }
}

